import React, { useState, useEffect } from "react";
import { FaAngleDoubleRight } from "react-icons/fa";
// ATTENTION!!!!!!!!!!
// I SWITCHED TO PERMANENT DOMAIN
const url = "https://course-api.com/react-tabs-project";

const App = () => {
  const [apiData, setApiData] = useState([]);
  const [companies, setCompanies] = useState([]);
  const [showCompany, setShowCompany] = useState({});

  const fetchData = async (url) => {
    const response = await fetch(url);
    const data = await response.json();
    const companyObj = data.map((datas) => {
      const newObj = { company: datas.company, id: datas.id };
      return newObj;
    });
    setShowCompany(data[0]);
    setCompanies(companyObj);
    setApiData(data);
  };

  useEffect(() => {
    fetchData(url);
  }, []);
  const setCompany = (id) => {
    const currentCompany = apiData.filter((data) => data.id === id)[0];
    setShowCompany(currentCompany);
  };

  console.log(showCompany);
  return (
    <section className="section">
      <div className="title">
        <h2>expierence</h2>
        <div className="underline"></div>
      </div>
      <div className="jobs-center">
        {/* btn container */}
        <div className="btn-container">
          {companies.map((item, index) => {
            return (
              <button
                key={index}
                className={`job-btn ${
                  item.id === showCompany.id && "active-btn"
                }`}
                onClick={() => setCompany(item.id)}
              >
                {item.company}
              </button>
            );
          })}
        </div>
        {/* job info */}
        <article className="job-info">
          <h3>{showCompany.title}</h3>
          <h4>{showCompany.company}</h4>
          <p className="job-date">{showCompany.dates}</p>
          {showCompany.duties &&
            showCompany.duties.map((duty, index) => {
              return (
                <div key={index} className="job-desc">
                  <FaAngleDoubleRight className="job-icon"></FaAngleDoubleRight>
                  <p>{duty}</p>
                </div>
              );
            })}
        </article>
      </div>
    </section>
  );
};

export default App;
